<!--
 * @Author: linxu
 * @Date: 2021-11-12 14:32:29
 * @LastEditors: Demon
 * @LastEditTime: 2022-9-1 10:06:00
 * @Description: 登录页
-->
<template>
  <div id="login">
    <div class="login-content">
      <el-image class="logo" fit="scale-down" :src="logoUrl"></el-image>

      <el-form ref="loginForm" class="login-form" label-width="0px" :model="form" :rules="rules">
        <h2>智能装备动力服务租赁平台</h2>
        <el-form-item label="" prop="userId">
          <el-input
            v-model="form.userId"
            placeholder="请输入用户名称"
            @keyup.enter.native="signIn"
            autofocus
          >
            <svg-icon slot="prefix" icon-class="Icon-login-account" />
          </el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input
            type="password"
            v-model="form.password"
            placeholder="请输入登录密码"
            @keyup.enter.native="signIn"
          >
            <svg-icon slot="prefix" icon-class="Icon-login-psd" />
          </el-input>
        </el-form-item>
        <!-- <el-form-item> -->
        <el-button class="login-btn" type="primary" :loading="loading" @click="signIn"
          >登录</el-button
        >
        <!-- </el-form-item> -->
      </el-form>
      <div class="concat">
        <el-image :src="fpssUrl"></el-image>
        <el-image :src="yunUrl"></el-image>
      </div>
    </div>
    <p class="clearfix text-muted">
      <span class="d-xs-block d-md-inline-block">
        Copyright © 2021
        <a href="http://www.fortrustpower.com/" target="_blank">上海孚创信息科技有限公司</a>
        , All rights reserved. - ICP:<a href="https://beian.miit.gov.cn" target="_blank"
          >沪ICP备18010178号-1</a
        >
      </span>
      <span class="d-xs-block d-md-inline-block">
        ©
        <i class="icon-heart5 pink"></i>
      </span>
    </p>
  </div>
</template>

<script>
// import Cookies from 'js-cookie'
import effect from './effect'

export default {
  name: 'Login',
  data() {
    return {
      logoUrl: require('@/assets/img/logo4k.png'),
      fpssUrl: require('@/assets/img/fortrust-fpss.png'),
      yunUrl: require('@/assets/img/fortrust-yun.png'),
      form: {
        userId: '',
        password: ''
      },
      rules: {
        userId: [{ required: true, message: '请输入用户名', trigger: 'blur' }],
        password: [{ required: true, message: '请输入用户密码', trigger: 'blur' }]
      },
      loading: false
    }
  },
  methods: {
    signIn() {
      this.$refs['loginForm'].validate((valid) => {
        if (valid) {
          this.login()
        } else {
        }
      })
    },

    async login() {
      // this.$router.push('/screendata');
      this.loading = true
      //  加密后登录
      let params = {
        model: {
          ...this.form,
          Type: 1
        }
      }
      const res = await this.$api.login.login(params)
      if (!res.IsSuccess) {
        this.$message({
          type: 'error',
          message: '登录失败！'
        })
        this.loading = false
        return
      }
      this.$wsCache.set('loginFlag', true)

      // this.$api.login.GetTest()
      // this.$api.login.GetAllGensetCollectorVersionVCors()
      // 查询登录用户信息
      const user = await this.$api.login.getUserInfo()
      this.loading = false

      if (user.IsSuccess) {
        this.$wsCache.set('userInfo', user.Data)
        // this.$router.push('/customerMonitoringScreen') // 客户首页
        // this.$router.push('/employeeMonitoringScreen') // 员工首页
        this.$router.push('/ms') // 运维监控大屏
      }
    }
  },
  mounted() {
    effect()
  },
  beforeDestroy() {
    try {
      document.querySelector('canvas[id*="c_n"]').remove()
    } catch (error) {}
  }
}
</script>

<style lang="less" scoped>
#login {
  height: 100%;
  overflow: hidden;
  padding-top: 15vh;
  text-align: center;
  box-sizing: border-box;
  background-size: 100% 100%;
  background-color: rgba(35, 62, 100, 0.8);
  /deep/ input {
    color: #e3e8f1 !important;
    border-color: #95a9ca66 !important;
    background-color: #768aae66 !important;
  }
  .logo {
    height: 80x;
    width: 180px;
  }
  .login-content {
    z-index: 2;
    position: relative;
  }
  .login-form {
    width: 193px;
    padding: 40px;
    border-radius: 8px;
    text-align: center;
    background: #3e5b92b0;
    margin: 26px auto 50px auto;
    border: 1px solid #95a9ca;
    h2 {
      color: #fff;
      font-size: 16px;
      font-weight: 300;
      margin: 0 0 20px;
      line-height: 30px;
    }
    .login-btn {
      width: 100%;
      margin: 15px 0 0 0;
      border-color: #95a9ca50 !important;
      background-color: #153c89 !important;
      &:hover {
        transition: 0.3s;
        border-color: #1abc9c !important;
        background-color: #153c8950 !important;
      }
    }
  }
  .concat {
    .el-image {
      width: 121px;
      height: 121px;
      &:first-child {
        margin-right: 46px;
      }
    }
  }

  .text-muted {
    z-index: 2;
    width: 100%;
    bottom: 16px;
    position: absolute;
    text-align: center;
    color: #818a91 !important;
    a {
      color: #337ab7;
    }
  }
}
@media screen and (max-width: 600px) {
  #login {
    background-image: url(../../assets/img/bg-1366x768.jpg);
  }
}
@media screen and (min-width: 600px) {
  #login {
    background-image: url(../../assets/img/bg-1366x768.jpg);
  }
}
@media screen and (min-width: 992px) {
  #login {
    background-image: url(../../assets/img/bg-1366x768.jpg);
  }
}
@media screen and (min-width: 1200px) {
  #login {
    background-image: url(../../assets/img/bg-1920x1080.jpg);
  }
}
@media screen and (min-width: 2000px) {
  #login {
    background-image: url(../../assets/img/bg-3840x2160.jpg);
  }
}
</style>



